* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Connect Design System v4.0 - Green Theme */
:root,
[data-theme="dark"] {
  /* Основной цвет - зелёный как в десктоп версии Connect */
  --primary: #00C853;
  --primary-dark: #00A843;
  --primary-light: #69F0AE;
  --primary-glow: rgba(0, 200, 83, 0.4);

  /* Background colors - Deeper, richer tones */
  --bg-primary: #080A0B;
  --bg-secondary: #0D1012;
  --bg-tertiary: #141819;
  --bg-modifier-hover: rgba(255, 255, 255, 0.05);
  --bg-modifier-selected: rgba(255, 255, 255, 0.1);
  --bg-accent: #1A1F21;
  --bg-elevated: #1A1F21;
  --bg-card: #141819;
  --bg-deep: #080A0B;
  --bg-glass: rgba(13, 16, 18, 0.9);
  --bg-floating: rgba(20, 24, 25, 0.95);

  /* Text colors */
  --text-primary: #f5f5f7;
  --text-secondary: #a1a1aa;
  --text-muted: #71717a;
  --text-faded: #52525b;
  --text-link: #00C853;

  /* Primary accent - Green */
  --accent: #00C853;
  --accent-dark: #00A843;
  --accent-light: #69F0AE;
  --accent-soft: rgba(0, 200, 83, 0.15);
  --accent-glow: rgba(0, 200, 83, 0.4);
  --accent-gradient: linear-gradient(135deg, #00C853 0%, #00A843 100%);

  /* Secondary accent - Cyan */
  --accent-secondary: #00B4D8;
  --accent-secondary-soft: rgba(0, 180, 216, 0.15);

  /* Accent colors */
  --accent-blue: #00B4D8;
  --accent-purple: #7B2CBF;
  --accent-orange: #fb923c;
  --accent-pink: #f472b6;
  --accent-yellow: #facc15;
  --accent-green: #00C853;

  /* Status colors */
  --success: #00C853;
  --danger: #f87171;
  --warning: #fbbf24;
  --online: #00C853;
  --idle: #fbbf24;
  --dnd: #f87171;
  --offline: #71717a;
  --streaming: #7B2CBF;

  /* Borders */
  --border: rgba(255, 255, 255, 0.06);
  --border-strong: rgba(255, 255, 255, 0.1);
  --border-accent: rgba(0, 200, 83, 0.3);

  /* Shadows */
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.3);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.5);
  --shadow-glow: 0 0 20px rgba(0, 200, 83, 0.3);

  /* Scrollbar */
  --scrollbar-track: transparent;
  --scrollbar-thumb: rgba(255, 255, 255, 0.1);
  --scrollbar-thumb-hover: rgba(255, 255, 255, 0.2);

  /* Gradients */
  --gradient-primary: linear-gradient(135deg, #00C853 0%, #00A843 100%);
  --gradient-secondary: linear-gradient(135deg, #00B4D8 0%, #00C853 100%);
  --gradient-dark: linear-gradient(180deg, rgba(8, 10, 11, 0) 0%, rgba(8, 10, 11, 1) 100%);

  /* Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 24px;
  --radius-full: 9999px;

  /* Animation timings */
  --transition-fast: 150ms;
  --transition-normal: 300ms;
  --transition-slow: 500ms;
  --transition-pulse: 1500ms;
  --ease-out-cubic: cubic-bezier(0.33, 1, 0.68, 1);
}

/* Light Theme */
[data-theme="light"] {
  --primary: #00A843;
  --primary-dark: #008836;
  --primary-light: #00C853;
  --primary-glow: rgba(0, 168, 67, 0.3);

  --bg-primary: #fafafa;
  --bg-secondary: #f4f4f5;
  --bg-tertiary: #e4e4e7;
  --bg-modifier-hover: rgba(0, 0, 0, 0.04);
  --bg-modifier-selected: rgba(0, 0, 0, 0.08);
  --bg-accent: #ffffff;
  --bg-elevated: #ffffff;
  --bg-card: #ffffff;
  --bg-deep: #e4e4e7;
  --bg-glass: rgba(255, 255, 255, 0.8);
  --bg-floating: rgba(255, 255, 255, 0.95);

  --text-primary: #18181b;
  --text-secondary: #52525b;
  --text-muted: #71717a;
  --text-faded: #a1a1aa;
  --text-link: #00A843;

  --accent: #00A843;
  --accent-dark: #008836;
  --accent-light: #00C853;
  --accent-soft: rgba(0, 168, 67, 0.1);
  --accent-glow: rgba(0, 168, 67, 0.3);
  --accent-gradient: linear-gradient(135deg, #00C853 0%, #00A843 100%);

  --success: #00A843;
  --danger: #ef4444;
  --warning: #f59e0b;
  --online: #00A843;
  --idle: #f59e0b;
  --dnd: #ef4444;
  --offline: #a1a1aa;
  --streaming: #7B2CBF;

  --border: rgba(0, 0, 0, 0.06);
  --border-strong: rgba(0, 0, 0, 0.1);
  --border-accent: rgba(0, 168, 67, 0.3);

  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.15);
  --shadow-glow: 0 0 20px rgba(0, 168, 67, 0.2);

  --scrollbar-track: transparent;
  --scrollbar-thumb: rgba(0, 0, 0, 0.1);
  --scrollbar-thumb-hover: rgba(0, 0, 0, 0.2);

  --gradient-primary: linear-gradient(135deg, #00C853 0%, #00A843 100%);
  --gradient-secondary: linear-gradient(135deg, #00B4D8 0%, #00C853 100%);
}

/* Import Inter font */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: background-color 0.3s ease, color 0.3s ease;
  line-height: 1.5;
  letter-spacing: -0.01em;
}

code {
  font-family: 'JetBrains Mono', 'Fira Code', monospace;
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-track);
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb);
  border-radius: var(--radius-full);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--scrollbar-thumb-hover);
}

/* Links */
a {
  color: var(--text-link);
  text-decoration: none;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--accent-light);
}

/* Selection */
::selection {
  background: var(--accent-soft);
  color: var(--text-primary);
}

/* Focus styles */
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}

/* Voice user hover */
.voice-user-clickable:hover {
  background-color: var(--bg-modifier-hover) !important;
}

/* Modern Button Base */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 500;
  border: none;
  border-radius: var(--radius-md);
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: var(--shadow-sm);
}

.btn-primary:hover {
  box-shadow: var(--shadow-glow);
  transform: translateY(-1px);
}

.btn-secondary {
  background: var(--bg-tertiary);
  color: var(--text-primary);
}

.btn-secondary:hover {
  background: var(--bg-accent);
}

.btn-ghost {
  background: transparent;
  color: var(--text-secondary);
}

.btn-ghost:hover {
  background: var(--bg-modifier-hover);
  color: var(--text-primary);
}

/* Glass Card */
.glass-card {
  background: var(--bg-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
}

/* Theme Switch - Modern */
.theme-switch {
  position: relative;
  display: inline-block;
  width: 52px;
  height: 28px;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.theme-slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background: var(--bg-tertiary);
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: var(--radius-full);
  border: 1px solid var(--border);
}

.theme-slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 3px;
  background: var(--text-primary);
  transition: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: 50%;
  box-shadow: var(--shadow-sm);
}

input:checked + .theme-slider {
  background: var(--gradient-primary);
  border-color: transparent;
}

input:checked + .theme-slider:before {
  transform: translateX(24px);
  background: white;
}

/* Animations */
@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes speaking-pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 var(--primary-glow);
  }
  50% {
    box-shadow: 0 0 0 4px var(--primary-glow);
  }
}

@keyframes ring {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(15deg);
  }
  20% {
    transform: rotate(-15deg);
  }
  30% {
    transform: rotate(10deg);
  }
  40% {
    transform: rotate(-10deg);
  }
  50% {
    transform: rotate(5deg);
  }
  60% {
    transform: rotate(-5deg);
  }
  70%, 100% {
    transform: rotate(0deg);
  }
}

.animate-slide-up {
  animation: slideUp 0.3s ease-out;
}

.animate-fade-in {
  animation: fadeIn 0.2s ease-out;
}

.animate-scale-in {
  animation: scaleIn 0.2s ease-out;
}

.animate-speaking-pulse {
  animation: speaking-pulse var(--transition-pulse) ease-in-out infinite;
}

.animate-ring {
  animation: ring 1s ease-in-out infinite;
}

/* Responsive */
@media (max-width: 768px) {
  .mobile-hide-server-list {
    display: none !important;
  }

  .mobile-channel-overlay {
    position: fixed !important;
    left: 0 !important;
    top: 0 !important;
    bottom: 0 !important;
    width: 300px !important;
    z-index: 100 !important;
    transform: translateX(-100%);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }

  .mobile-channel-overlay.open {
    transform: translateX(0);
  }

  .mobile-backdrop {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(4px);
    z-index: 99;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
  }

  .mobile-backdrop.visible {
    opacity: 1;
    pointer-events: auto;
  }

  .mobile-header {
    display: flex !important;
    align-items: center;
    padding: 16px;
    background: var(--bg-glass);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--border);
    gap: 16px;
  }

  .mobile-menu-btn {
    background: var(--bg-tertiary);
    border: none;
    border-radius: var(--radius-md);
    color: var(--text-primary);
    font-size: 20px;
    cursor: pointer;
    padding: 8px 12px;
    transition: background 0.2s;
  }

  .mobile-menu-btn:hover {
    background: var(--bg-accent);
  }

  .mobile-modal-fullscreen {
    width: 100% !important;
    height: 100% !important;
    max-height: 100% !important;
    border-radius: 0 !important;
  }

  .mobile-voice-status {
    padding: 8px !important;
  }

  .mobile-user-section {
    padding: 8px 12px !important;
  }

  .mobile-modal {
    width: 92% !important;
    max-width: 400px !important;
  }
}

@media (max-width: 480px) {
  .mobile-small-modal {
    width: 96% !important;
    padding: 16px !important;
  }

  .mobile-small-text {
    font-size: 14px !important;
  }

  .mobile-compact-channel {
    padding: 8px 12px !important;
  }

  .mobile-small-avatar {
    width: 36px !important;
    height: 36px !important;
    font-size: 14px !important;
  }
}

@media (max-height: 500px) and (orientation: landscape) {
  .mobile-landscape-compact {
    padding: 6px 12px !important;
  }

  .mobile-landscape-hide {
    display: none !important;
  }
}

/* Utility classes */
.text-gradient {
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.border-gradient {
  border: 1px solid transparent;
  background: linear-gradient(var(--bg-card), var(--bg-card)) padding-box,
              var(--gradient-primary) border-box;
}

.glow {
  box-shadow: var(--shadow-glow);
}

/* Attachment menu button - показываем при наведении на wrapper */
[class*="attachmentWrapper"]:hover button,
div[style*="position: relative"][style*="display: inline-block"]:hover button[style*="position: absolute"] {
  opacity: 1 !important;
}
